}
.foundAd {
  margin-top: 90px;
  /* text-align: center; */
}
.foundAd-button {
  height: 40px;
  margin-bottom: 20px;
}
.ads-wrapper {
  display: block;
  width: 100%;
}
.delete-button {
  float: right;
  /* background-color: #d80669;
  color: white;
  border: 1px solid #82003d;
  border-radius: 3px; */
  width: 30px;
  height: 30px;
  margin: 8px 8px 0px 0px;
}
.ads-container {
  background: #f5f5f5;
  margin: 10px 0px;
  padding: 15px 15px;
  border: 1px solid #CCC;

  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE*/
  width: 100%;
  /* border-radius: 3px; */
}
.addContainer {
  display: block;
  justify-content: center;
  align-items: center;

  width: 100%;
  /* height: 100vh; */
}
.addFields {
  /* width: 100%; /*Makes the form take all the possible width */ */
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE*/
  width: 100%;

  background-color: #f5f5f5;
  border-radius: 5px;
  border: 1px solid #CCC;
  padding: 20px;
}
h1 {
  text-align: center;
}
h2 {
  text-align: left;
  padding-top: 7px;
}
input, textarea {
  /*width: -moz-min-content;
  width: min-content; */
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE*/
  width: 100%;
}
input {
  height: 30px;
  background-color: white;
  border-radius: 3px;
  border: 1px solid #AAA;
  padding-left: 8px;
}
textarea:focus, input:focus {
  border: 1px solid #d80669;
}

/* [name="classAdTitle"] {
  height: 30px;
} */
[name="classAdDescription"] {
  height: 40vh;
  font-size: 14px;
  border: 1px solid #AAA;
  border-radius: 3px;
  padding: 8px;
  resize: none;
  /* max-height: 40vh;
  min-height: 200px; */
}
[name="classAdPrice"] {
  width: 20%;
  margin: 24px 20px 0px 20px;
}
.priceRelated {
  float: left;
  margin-bottom: 0px;
}
.priceWrapper {
  width: 100%;
  margin-top: 20px;
  height: inherit;
  overflow: auto;
}
.formButton {
  padding: 8px 15px;
  font-size: 16px;
  border-radius: 3px;
  margin-top: 22px;
  margin-right: 1.5%;
  float: right;
}
.important-button {
  color: white;
  background-color: #d80669;
  border: 1px solid #82003d;
  border-radius: 3px;
}
[name="cancel"] {
  color: black;
  background-color: #EBEBEB;
  border: 1px solid #AAA;

  /* background-color: inherit;
  color: #d80669;
  border-style: solid;
  border-color: transparent; */
}
.important-button:active {
  background-color: #82003d;
}
[name="cancel"]:active {
  background-color: #AAA;
}
.formButton:hover {
  cursor: pointer;
}


